export function BookmarksPagination({
    currentPage = 1,
    totalPages = 3,
    onPageChange
}: BookmarksPaginationProps) {

    const handlePageChange = (page: number) => {
        if (onPageChange) {
            onPageChange(page)
        }
    }

    return (
        <div style={{
            marginTop: '16px',
            marginBottom: '32px',
            display: 'flex',
            justifyContent: 'center'
        }}>
            <Pagination
                currentPage={currentPage}
                totalPages={totalPages}
                onPageChange={handlePageChange}
            />
        </div>
    )
} 