'use client'

import { useState, useEffect } from 'react'
import { Icon, GradientButton } from '@/components/ui'
import type { FontSizeControllerProps } from './FontSizeController.types'

/**
 * FontSizeController å­—ä½“å¤§å°æ§åˆ¶å™¨
 * 
 * ğŸ¯ åŠŸèƒ½ï¼š
 * - æä¾›å­—ä½“å¤§å°è°ƒæ•´ç•Œé¢
 * - æ¯›ç»ç’ƒé£æ ¼é€‰æ‹©å™¨
 * - é¢„è®¾å­—ä½“å¤§å°é€‰é¡¹
 * - ç‚¹å‡»å¤–éƒ¨è‡ªåŠ¨å…³é—­
 * 
 * ğŸ“± å“åº”å¼ï¼š
 * - è‡ªé€‚åº”å®šä½
 * - æ¯›ç»ç’ƒèƒŒæ™¯
 */
export function FontSizeController({ 
    fontSize, 
    onFontSizeChange,
    className = '' 
}: FontSizeControllerProps) {
    const [showFontSelector, setShowFontSelector] = useState(false)

    // å­—ä½“å¤§å°é€‰é¡¹
    const fontSizeOptions = [
        { size: 14, label: 'å°', desc: '14px' },
        { size: 16, label: 'ä¸­', desc: '16px' },
        { size: 18, label: 'å¤§', desc: '18px' },
        { size: 20, label: 'ç‰¹å¤§', desc: '20px' }
    ]

    // ç‚¹å‡»å¤–éƒ¨å…³é—­å­—ä½“é€‰æ‹©å™¨
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (showFontSelector) {
                const fontSelector = document.querySelector('.font-selector')
                const adjustButton = document.querySelector('.font-adjust-container')

                if (fontSelector && adjustButton) {
                    if (!fontSelector.contains(event.target as Node) &&
                        !adjustButton.contains(event.target as Node)) {
                        setShowFontSelector(false)
                    }
                }
            }
        }

        if (showFontSelector) {
            document.addEventListener('mousedown', handleClickOutside)
        }

        return () => {
            document.removeEventListener('mousedown', handleClickOutside)
        }
    }, [showFontSelector])

    // å­—ä½“å¤§å°è°ƒèŠ‚åŠŸèƒ½
    const handleFontSizeAdjust = () => {
        setShowFontSelector(!showFontSelector)
    }

    const handleFontSizeSelect = (newSize: number) => {
        onFontSizeChange(newSize)
        setShowFontSelector(false)
    }

    return (
        <div className={`font-adjust-container ${className}`} style={{ position: 'relative' }}>
            {/* CSSåŠ¨ç”»å®šä¹‰ */}
            <style jsx>{`
                @keyframes fadeIn {
                    from {
                        opacity: 0;
                        transform: translateY(10px);
                    }
                    to {
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
            `}</style>

            <GradientButton
                variant="outline" 
                size="md"
                onClick={handleFontSizeAdjust}
                title="è°ƒæ•´å­—ä½“å¤§å°"
            >
                <Icon name="settings-icon" size="sm" />
                è°ƒæ•´
            </GradientButton>

            {/* å­—ä½“å¤§å°é€‰æ‹©å™¨ */}
            {showFontSelector && (
                <div className="font-selector" style={{
                    position: 'absolute',
                    top: '100%',
                    right: '0',
                    marginTop: '8px',
                    background: 'var(--color-bg-glass)',
                    backdropFilter: 'blur(12px)',
                    WebkitBackdropFilter: 'blur(12px)',
                    border: '1px solid var(--color-border-primary)',
                    borderRadius: '12px',
                    padding: '12px',
                    minWidth: '180px',
                    zIndex: 10000,
                    boxShadow: '0 8px 32px rgba(0, 0, 0, 0.3)',
                    animation: 'fadeIn 0.2s ease'
                }}>
                    <div style={{
                        fontSize: 'var(--font-size-xs)',
                        color: 'var(--color-text-muted)',
                        marginBottom: '12px',
                        textAlign: 'center',
                        fontWeight: '500'
                    }}>
                        é€‰æ‹©å­—ä½“å¤§å°
                    </div>
                    {fontSizeOptions.map(({ size, label, desc }) => (
                        <button
                            key={size}
                            onClick={() => handleFontSizeSelect(size)}
                            style={{
                                width: '100%',
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center',
                                padding: '10px 12px',
                                margin: '2px 0',
                                background: fontSize === size
                                    ? 'linear-gradient(90deg, rgba(59, 130, 246, 0.2), rgba(139, 92, 246, 0.2))'
                                    : 'transparent',
                                border: fontSize === size
                                    ? '1px solid rgba(59, 130, 246, 0.3)'
                                    : '1px solid transparent',
                                borderRadius: '8px',
                                cursor: 'pointer',
                                color: fontSize === size
                                    ? 'var(--color-text-primary)'
                                    : 'var(--color-text-muted)',
                                fontSize: 'var(--font-size-sm)',
                                fontWeight: fontSize === size ? '500' : 'normal',
                                transition: 'all 0.2s ease'
                            }}
                            onMouseEnter={(e) => {
                                if (fontSize !== size) {
                                    e.currentTarget.style.background = 'rgba(255, 255, 255, 0.05)'
                                    e.currentTarget.style.color = 'var(--color-text-primary)'
                                }
                            }}
                            onMouseLeave={(e) => {
                                if (fontSize !== size) {
                                    e.currentTarget.style.background = 'transparent'
                                    e.currentTarget.style.color = 'var(--color-text-muted)'
                                }
                            }}
                        >
                            <span>{label}</span>
                            <span style={{ 
                                fontSize: 'var(--font-size-xs)', 
                                opacity: 0.7 
                            }}>
                                {desc}
                            </span>
                        </button>
                    ))}
                </div>
            )}
        </div>
    )
} 