'use client'

import { useEffect, useRef, useState } from 'react'
import * as THREE from 'three'
import { GradientText } from '@/components/ui'

/**
 * 3DåŠ¨ç”»åŒºå—ç»„ä»¶ - ThreeDSection
 * 
 * ç‰¹è‰²åŠŸèƒ½ï¼š
 * - AIç§‘æŠ€æ„Ÿç²’å­æ•ˆæœ
 * - æ¯›ç»ç’ƒèƒŒæ™¯æ•ˆæœ
 * - å“åº”å¼3Dåœºæ™¯
 * - æ€§èƒ½ä¼˜åŒ–å’Œè‡ªé€‚åº”
 */
export function ThreeDSection() {
    const mountRef = useRef<HTMLDivElement>(null)
    const sceneRef = useRef<THREE.Scene | null>(null)
    const rendererRef = useRef<THREE.WebGLRenderer | null>(null)
    const animationIdRef = useRef<number | null>(null)
    const [isLoaded, setIsLoaded] = useState(false)
    // æ·»åŠ å¼ºåˆ¶é‡æ–°åˆå§‹åŒ–çŠ¶æ€
    const [forceReload, setForceReload] = useState(0)
    const cameraRef = useRef<THREE.PerspectiveCamera | null>(null)

    useEffect(() => {
        // å¼ºåˆ¶æ¸…ç†ä¹‹å‰çš„çŠ¶æ€
        setIsLoaded(false)

        // å»¶è¿Ÿåˆå§‹åŒ–ç¡®ä¿DOMå®Œå…¨å‡†å¤‡å°±ç»ª
        const initTimer = setTimeout(() => {
            if (!mountRef.current) {
                // DOMæœªå‡†å¤‡å¥½ï¼Œå»¶è¿Ÿé‡è¯•
                setForceReload(prev => prev + 1)
                return
            }

            // å¼ºåˆ¶æ¸…ç†å¯èƒ½æ®‹ç•™çš„renderer
            if (rendererRef.current && mountRef.current.contains(rendererRef.current.domElement)) {
                mountRef.current.removeChild(rendererRef.current.domElement)
                rendererRef.current.dispose()
                rendererRef.current = null
            }

            // åˆ›å»ºåœºæ™¯
            const scene = new THREE.Scene()
            sceneRef.current = scene

            // åˆ›å»ºç›¸æœº
            const camera = new THREE.PerspectiveCamera(
                75,
                mountRef.current.clientWidth / mountRef.current.clientHeight,
                0.1,
                1000
            )
            camera.position.z = 8
            cameraRef.current = camera

            // åˆ›å»ºæ¸²æŸ“å™¨
            const renderer = new THREE.WebGLRenderer({
                alpha: true,
                antialias: true,
                powerPreference: "high-performance",
                preserveDrawingBuffer: false
            })

            // æ£€æŸ¥WebGLæ˜¯å¦å¯ç”¨
            if (!renderer.getContext()) {
                console.warn('WebGL not available, falling back to basic rendering')
                setIsLoaded(true)
                return
            }

            renderer.setSize(mountRef.current.clientWidth, mountRef.current.clientHeight)
            renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2)) // æ€§èƒ½ä¼˜åŒ–
            renderer.setClearColor(0x000000, 0) // é€æ˜èƒŒæ™¯

            // ç¡®ä¿DOMå…ƒç´ å­˜åœ¨å†æ·»åŠ 
            if (mountRef.current) {
                mountRef.current.appendChild(renderer.domElement)
                rendererRef.current = renderer
            } else {
                renderer.dispose()
                return
            }

            // åˆ›å»ºç²’å­ç³»ç»Ÿ
            const particleGeometry = new THREE.BufferGeometry()
            const particleCount = 300 // å¢åŠ ç²’å­æ•°é‡ä»¥è¡¥å¿å»é™¤çš„å‡ ä½•ä½“
            const positions = new Float32Array(particleCount * 3)
            const colors = new Float32Array(particleCount * 3)

            for (let i = 0; i < particleCount * 3; i += 3) {
                // éšæœºä½ç½®
                positions[i] = (Math.random() - 0.5) * 20     // x
                positions[i + 1] = (Math.random() - 0.5) * 20 // y
                positions[i + 2] = (Math.random() - 0.5) * 20 // z

                // éšæœºé¢œè‰²ï¼ˆè“ç´«è‰²è°ƒï¼‰
                const colorChoice = Math.random()
                if (colorChoice < 0.33) {
                    colors[i] = 0.231     // è“è‰² #3B82F6
                    colors[i + 1] = 0.510
                    colors[i + 2] = 0.965
                } else if (colorChoice < 0.66) {
                    colors[i] = 0.545     // ç´«è‰² #8B5CF6
                    colors[i + 1] = 0.361
                    colors[i + 2] = 0.965
                } else {
                    colors[i] = 0.063     // ç»¿è‰² #10B981
                    colors[i + 1] = 0.725
                    colors[i + 2] = 0.506
                }
            }

            particleGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
            particleGeometry.setAttribute('color', new THREE.BufferAttribute(colors, 3))

            const particleMaterial = new THREE.PointsMaterial({
                size: 0.08, // ç•¥å¾®å¢å¤§ç²’å­å°ºå¯¸
                vertexColors: true,
                transparent: true,
                opacity: 0.9, // ç•¥å¾®æé«˜é€æ˜åº¦
                blending: THREE.AdditiveBlending
            })

            const particles = new THREE.Points(particleGeometry, particleMaterial)
            scene.add(particles)

            // åŠ¨ç”»å‡½æ•°
            let time = 0
            const animate = () => {
                // æ£€æŸ¥ç»„ä»¶æ˜¯å¦ä»ç„¶æŒ‚è½½
                if (!mountRef.current || !sceneRef.current || !rendererRef.current) {
                    return
                }

                time += 0.01

                // ç²’å­åŠ¨ç”»
                const positions = particles.geometry.attributes.position.array as Float32Array
                for (let i = 0; i < positions.length; i += 3) {
                    positions[i + 1] += Math.sin(time + i) * 0.001 // yè½´æµ®åŠ¨
                    positions[i] += Math.cos(time + i * 0.1) * 0.0005 // xè½´å¾®åŠ¨
                }
                particles.geometry.attributes.position.needsUpdate = true

                // æ•´ä½“ç²’å­ç³»ç»Ÿæ—‹è½¬
                particles.rotation.y += 0.002

                try {
                    renderer.render(scene, camera)
                    animationIdRef.current = requestAnimationFrame(animate)
                } catch (error) {
                    console.warn('Three.js render error:', error)
                    return
                }
            }

            // å“åº”å¼å¤„ç†
            const handleResize = () => {
                if (!mountRef.current || !renderer || !camera) return

                const width = mountRef.current.clientWidth
                const height = mountRef.current.clientHeight

                camera.aspect = width / height
                camera.updateProjectionMatrix()
                renderer.setSize(width, height)
            }

            window.addEventListener('resize', handleResize)

            // å¯åŠ¨åŠ¨ç”»
            animate()
            setIsLoaded(true)

            // æ¸…ç†å‡½æ•°
            return () => {
                window.removeEventListener('resize', handleResize)

                if (animationIdRef.current) {
                    cancelAnimationFrame(animationIdRef.current)
                    animationIdRef.current = null
                }

                // æ¸…ç†Three.jsèµ„æº
                if (sceneRef.current) {
                    sceneRef.current.clear()
                    sceneRef.current = null
                }

                if (rendererRef.current) {
                    if (mountRef.current && mountRef.current.contains(rendererRef.current.domElement)) {
                        mountRef.current.removeChild(rendererRef.current.domElement)
                    }
                    rendererRef.current.dispose()
                    rendererRef.current = null
                }

                // æ¸…ç†ç²’å­ç³»ç»Ÿèµ„æº
                if (particleGeometry) particleGeometry.dispose()
                if (particleMaterial) particleMaterial.dispose()
            }
        }, 100) // å»¶è¿Ÿ100msç¡®ä¿DOMå®Œå…¨å‡†å¤‡å°±ç»ª

        // æ¸…ç†è®¡æ—¶å™¨
        return () => {
            clearTimeout(initTimer)
        }
    }, [forceReload]) // ä¾èµ–forceReloadç¡®ä¿ç»„ä»¶èƒ½é‡æ–°åˆå§‹åŒ–

    // æ·»åŠ é”™è¯¯è¾¹ç•Œå¤„ç†
    useEffect(() => {
        const errorHandler = (event: ErrorEvent) => {
            if (event.message.includes('WebGL') || event.message.includes('Three')) {
                console.warn('WebGL error detected, attempting to reinitialize:', event.message)
                setForceReload(prev => prev + 1)
            }
        }

        window.addEventListener('error', errorHandler)
        return () => window.removeEventListener('error', errorHandler)
    }, [])

    return (
        <section style={{
            position: 'relative',
            height: '400px',
            width: '100%',
            overflow: 'hidden',
            background: 'transparent',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
        }}>
            {/* èƒŒæ™¯æ¯›ç»ç’ƒæ•ˆæœ */}
            <div style={{
                position: 'absolute',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                background: 'var(--color-bg-glass)',
                backdropFilter: 'blur(8px)',
                WebkitBackdropFilter: 'blur(8px)',
                zIndex: 1
            }} />

            {/* æ¸å˜è£…é¥°èƒŒæ™¯ */}
            <div style={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                width: '600px',
                height: '300px',
                background: 'radial-gradient(ellipse, rgba(59, 130, 246, 0.1) 0%, rgba(139, 92, 246, 0.05) 50%, transparent 100%)',
                borderRadius: '50%',
                filter: 'blur(40px)',
                zIndex: 0
            }} />

            {/* 3Dåœºæ™¯å®¹å™¨ */}
            <div
                ref={mountRef}
                style={{
                    width: '100%',
                    height: '100%',
                    position: 'relative',
                    zIndex: 2,
                    opacity: isLoaded ? 1 : 0,
                    transition: 'opacity 0.5s ease-in-out'
                }}
            />

            {/* åŠ è½½æŒ‡ç¤ºå™¨ */}
            {!isLoaded && (
                <div style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    color: 'var(--color-text-secondary)',
                    fontSize: '14px',
                    zIndex: 3
                }}>
                    åŠ è½½3Dåœºæ™¯ä¸­...
                </div>
            )}

            {/* ä¸­å¿ƒæ–‡å­— - "AI å˜ç°ä¹‹è·¯ ä»å…¥é—¨åˆ°ç²¾é€š" */}
            <div style={{
                position: 'absolute',
                bottom: '40px',
                left: '50%',
                transform: 'translateX(-50%)',
                zIndex: 10,
                textAlign: 'center'
            }}>
                <GradientText
                    as="div"
                    size="3xl"
                    weight="semibold"
                    style={{
                        lineHeight: '32px',
                        whiteSpace: 'nowrap',
                        textShadow: '0 0 20px rgba(59, 130, 246, 0.3)'
                    }}
                >
                    AI å˜ç°ä¹‹è·¯
                </GradientText>
                <div style={{
                    color: 'var(--color-text-secondary)',
                    fontSize: 'var(--font-size-lg)',
                    fontFamily: 'var(--font-family-primary)',
                    fontWeight: '400',
                    lineHeight: '24px',
                    marginTop: '8px'
                }}>
                    ä»å…¥é—¨åˆ°ç²¾é€š
                </div>
            </div>

            {/* å“åº”å¼æ ·å¼ */}
            <style jsx>{`
                /* ä¸­ç­‰å±å¹•é€‚é… */
                @media (max-width: 1199px) {
                    section {
                        height: 350px !important;
                    }
                    
                    section > div:last-child > div:first-child {
                        font-size: 20px !important;
                        line-height: 28px !important;
                    }
                    
                    section > div:last-child > div:last-child {
                        font-size: 14px !important;
                        line-height: 20px !important;
                    }
                }
                
                /* ç§»åŠ¨ç«¯é€‚é… */
                @media (max-width: 767px) {
                    section {
                        height: 300px !important;
                    }
                    
                    section > div:last-child {
                        bottom: 30px !important;
                    }
                    
                    section > div:last-child > div:first-child {
                        font-size: 18px !important;
                        line-height: 24px !important;
                    }
                    
                    section > div:last-child > div:last-child {
                        font-size: 12px !important;
                        line-height: 18px !important;
                    }
                }
                
                /* è¶…å°å±å¹•é€‚é… */
                @media (max-width: 480px) {
                    section {
                        height: 250px !important;
                    }
                    
                    section > div:last-child > div:first-child {
                        font-size: 16px !important;
                        line-height: 22px !important;
                    }
                }
            `}</style>
        </section>
    )
} 