'use client'

import React, { useState, useEffect } from 'react';
import { usePayment, usePaymentPolling } from '@/lib/hooks/usePayment';
import { loadStripe, Stripe, StripeElements } from '@stripe/stripe-js';
import {
  Elements,
  CardElement,
  useStripe,
  useElements
} from '@stripe/react-stripe-js';
import Image from 'next/image';

interface StripeCheckoutProps {
  paymentData: {
    type: 'stripe_elements';
    clientSecret: string;
  };
  paymentNo: string;
  onSuccess: (result: any) => void;
  onError: (error: Error) => void;
  onCancel?: () => void;
}

// Stripeå®ä¾‹ç¼“å­˜
let stripePromise: Promise<Stripe | null> | null = null;

/**
 * è·å–Stripeå®ä¾‹
 */
const getStripe = async () => {
  if (!stripePromise) {
    try {
      // ä»åç«¯è·å–Stripeé…ç½®
      const response = await fetch('/api/payments/stripe/config');
      const config = await response.json();
      
      if (config.success && config.data.publishableKey) {
        stripePromise = loadStripe(config.data.publishableKey);
      } else {
        throw new Error('Stripeé…ç½®è·å–å¤±è´¥');
      }
    } catch (error) {
      console.error('è·å–Stripeé…ç½®å¤±è´¥:', error);
      throw error;
    }
  }
  return stripePromise;
};

/**
 * Stripeæ”¯ä»˜è¡¨å•ç»„ä»¶
 */
const StripePaymentForm: React.FC<{
  clientSecret: string;
  paymentNo: string;
  onSuccess: (result: any) => void;
  onError: (error: Error) => void;
  onCancel?: () => void;
}> = ({ clientSecret, paymentNo, onSuccess, onError, onCancel }) => {
  const stripe = useStripe();
  const elements = useElements();
  const [isProcessing, setIsProcessing] = useState(false);
  const [cardComplete, setCardComplete] = useState(false);
  const [cardError, setCardError] = useState<string | null>(null);
  const { cancelPayment } = usePayment();

  // æ”¯ä»˜çŠ¶æ€è½®è¯¢
  const { status, isPolling, startPolling, stopPolling } = usePaymentPolling(
    paymentNo,
    (status) => {
      if (status.status === 'success') {
        setIsProcessing(false);
        onSuccess({
          paymentNo: status.paymentNo,
          amount: status.amount,
          thirdPartyTransactionId: status.thirdPartyTransactionId
        });
      } else if (status.status === 'failed' || status.status === 'cancelled') {
        setIsProcessing(false);
        onError(new Error(status.message || 'æ”¯ä»˜å¤±è´¥'));
      }
    }
  );

  /**
   * å¤„ç†æ”¯ä»˜æäº¤
   */
  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();

    if (!stripe || !elements) {
      onError(new Error('Stripeæœªåˆå§‹åŒ–'));
      return;
    }

    const cardElement = elements.getElement(CardElement);
    if (!cardElement) {
      onError(new Error('æ”¯ä»˜è¡¨å•æœªæ‰¾åˆ°'));
      return;
    }

    if (!cardComplete) {
      onError(new Error('è¯·å®Œæ•´å¡«å†™ä¿¡ç”¨å¡ä¿¡æ¯'));
      return;
    }

    setIsProcessing(true);
    startPolling();

    try {
      // ç¡®è®¤æ”¯ä»˜
      const { error, paymentIntent } = await stripe.confirmCardPayment(clientSecret, {
        payment_method: {
          card: cardElement
        }
      });

      if (error) {
        setIsProcessing(false);
        stopPolling();
        onError(new Error(error.message || 'æ”¯ä»˜å¤±è´¥'));
      } else if (paymentIntent?.status === 'succeeded') {
        setIsProcessing(false);
        stopPolling();
        onSuccess({
          paymentNo,
          paymentIntentId: paymentIntent.id,
          amount: paymentIntent.amount
        });
      } else {
        // æ”¯ä»˜å¯èƒ½éœ€è¦è¿›ä¸€æ­¥éªŒè¯ï¼Œç»§ç»­è½®è¯¢
        console.log('æ”¯ä»˜éœ€è¦è¿›ä¸€æ­¥å¤„ç†:', paymentIntent?.status);
      }
    } catch (error) {
      setIsProcessing(false);
      stopPolling();
      onError(error as Error);
    }
  };

  /**
   * å¤„ç†å¡ç‰‡ä¿¡æ¯å˜åŒ–
   */
  const handleCardChange = (event: any) => {
    setCardComplete(event.complete);
    setCardError(event.error ? event.error.message : null);
  };

  /**
   * å–æ¶ˆæ”¯ä»˜
   */
  const handleCancel = async () => {
    setIsProcessing(false);
    stopPolling();
    
    if (paymentNo) {
      await cancelPayment(paymentNo);
    }
    
    onCancel?.();
  };

  return (
    <div className="stripe-payment-form">
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* ä¿¡ç”¨å¡è¾“å…¥åŒºåŸŸ */}
        <div className="card-input-section">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            ä¿¡ç”¨å¡ä¿¡æ¯
          </label>
          
          <div className="border rounded-lg p-4 bg-white">
            <CardElement
              onChange={handleCardChange}
              options={{
                style: {
                  base: {
                    fontSize: '16px',
                    color: '#374151',
                    fontFamily: '"Alibaba PuHuiTi 3.0", sans-serif',
                    '::placeholder': {
                      color: '#9CA3AF'
                    }
                  },
                  invalid: {
                    color: '#EF4444'
                  }
                },
                hidePostalCode: false
              }}
            />
          </div>
          
          {cardError && (
            <p className="mt-2 text-sm text-red-600">{cardError}</p>
          )}
        </div>

        {/* æ”¯ä»˜çŠ¶æ€æ˜¾ç¤º */}
        {isProcessing && (
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <div className="flex items-center">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600 mr-3"></div>
              <span className="text-blue-800 text-sm">æ­£åœ¨å¤„ç†æ”¯ä»˜...</span>
            </div>
            <p className="text-blue-600 text-xs mt-1">
              è¯·ä¸è¦å…³é—­æˆ–åˆ·æ–°é¡µé¢
            </p>
          </div>
        )}

        {/* æ“ä½œæŒ‰é’® */}
        <div className="flex gap-3">
          <button
            type="button"
            onClick={handleCancel}
            disabled={isProcessing}
            className="flex-1 bg-gray-100 text-gray-700 py-3 px-4 rounded-lg hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            å–æ¶ˆæ”¯ä»˜
          </button>
          
          <button
            type="submit"
            disabled={!stripe || !cardComplete || isProcessing}
            className="flex-1 bg-purple-600 text-white py-3 px-4 rounded-lg hover:bg-purple-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
          >
            {isProcessing ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                å¤„ç†ä¸­...
              </>
            ) : (
              'ç«‹å³æ”¯ä»˜'
            )}
          </button>
        </div>
      </form>

      {/* å®‰å…¨æç¤º */}
      <div className="mt-6 p-3 bg-gray-50 rounded-lg">
        <div className="flex items-center text-sm text-gray-600">
          <svg className="w-4 h-4 mr-2 text-green-500" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M10 1L5 6v6a1 1 0 001 1h8a1 1 0 001-1V6l-5-5zM8 13a1 1 0 102 0 1 1 0 00-2 0z" clipRule="evenodd" />
          </svg>
          <span>æ‚¨çš„æ”¯ä»˜ä¿¡æ¯é€šè¿‡SSLåŠ å¯†ä¼ è¾“ï¼Œå®‰å…¨å¯é </span>
        </div>
      </div>
    </div>
  );
};

/**
 * Stripeæ”¯ä»˜ç»„ä»¶ä¸»ä½“
 */
export const StripeCheckout: React.FC<StripeCheckoutProps> = ({
  paymentData,
  paymentNo,
  onSuccess,
  onError,
  onCancel
}) => {
  const [stripeInstance, setStripeInstance] = useState<Stripe | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const initializeStripe = async () => {
      try {
        const stripe = await getStripe();
        setStripeInstance(stripe);
      } catch (error) {
        onError(error as Error);
      } finally {
        setIsLoading(false);
      }
    };

    initializeStripe();
  }, [onError]);

  if (isLoading) {
    return (
      <div className="stripe-checkout-loading">
        <div className="text-center py-8">
          <div className="mb-4">
            <Image
              src="/icons/stripe-large.svg"
              alt="Stripe"
              width={64}
              height={64}
              className="mx-auto"
            />
          </div>
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-purple-600 mx-auto mb-4"></div>
          <p className="text-gray-600">æ­£åœ¨åˆå§‹åŒ–æ”¯ä»˜...</p>
        </div>
      </div>
    );
  }

  if (!stripeInstance) {
    return (
      <div className="stripe-checkout-error">
        <div className="text-center py-8">
          <div className="mb-4">
            <Image
              src="/icons/stripe-large.svg"
              alt="Stripe"
              width={64}
              height={64}
              className="mx-auto opacity-50"
            />
          </div>
          <p className="text-red-600 mb-4">Stripeæ”¯ä»˜åˆå§‹åŒ–å¤±è´¥</p>
          <button
            onClick={onCancel}
            className="text-blue-600 hover:text-blue-800"
          >
            è¿”å›é€‰æ‹©å…¶ä»–æ”¯ä»˜æ–¹å¼
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="stripe-checkout">
      <div className="text-center mb-6">
        <div className="mb-4">
          <Image
            src="/icons/stripe-large.svg"
            alt="Stripe"
            width={64}
            height={64}
            className="mx-auto"
          />
        </div>
        
        <h3 className="text-xl font-semibold text-gray-900 mb-2">
          ä¿¡ç”¨å¡æ”¯ä»˜
        </h3>
        
        <p className="text-gray-600">
          æ”¯æŒVisaã€Mastercardã€American Expressç­‰ä¸»æµä¿¡ç”¨å¡
        </p>
      </div>

      <Elements
        stripe={stripeInstance}
        options={{
          clientSecret: paymentData.clientSecret,
          appearance: {
            theme: 'stripe',
            variables: {
              colorPrimary: '#7C3AED',
              colorBackground: '#ffffff',
              colorText: '#374151',
              colorDanger: '#EF4444',
              fontFamily: '"Alibaba PuHuiTi 3.0", sans-serif',
              spacingUnit: '4px',
              borderRadius: '8px'
            }
          }
        }}
      >
        <StripePaymentForm
          clientSecret={paymentData.clientSecret}
          paymentNo={paymentNo}
          onSuccess={onSuccess}
          onError={onError}
          onCancel={onCancel}
        />
      </Elements>

      {/* æ”¯æŒçš„æ”¯ä»˜æ–¹å¼å›¾æ ‡ */}
      <div className="mt-6 text-center">
        <p className="text-xs text-gray-500 mb-2">æ”¯æŒçš„æ”¯ä»˜æ–¹å¼</p>
        <div className="flex justify-center gap-2">
          <div className="w-8 h-5 bg-gray-100 rounded flex items-center justify-center">
            <span className="text-xs text-gray-600">VISA</span>
          </div>
          <div className="w-8 h-5 bg-gray-100 rounded flex items-center justify-center">
            <span className="text-xs text-gray-600">MC</span>
          </div>
          <div className="w-8 h-5 bg-gray-100 rounded flex items-center justify-center">
            <span className="text-xs text-gray-600">AMEX</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StripeCheckout;