'use client'

import React, { useState, useEffect, useRef } from 'react'
import { useToast } from '@/components/ui'
import { verificationManager, formatTimeRemaining } from '@/lib/verificationManager'

export interface EmailVerificationProps {
  email: string
  userName?: string
  type?: 'registration' | 'login'
  onVerificationSuccess: (email: string) => void
  onCancel?: () => void
  className?: string
}

/**
 * é‚®ç®±éªŒè¯ç ç»„ä»¶
 * 
 * åŠŸèƒ½ç‰¹æ€§ï¼š
 * - 6ä½æ•°å­—éªŒè¯ç è¾“å…¥
 * - è‡ªåŠ¨å‘é€éªŒè¯ç 
 * - å€’è®¡æ—¶é‡å‘æœºåˆ¶
 * - éªŒè¯å¤±è´¥æç¤º
 * - è‡ªåŠ¨èšç„¦å’Œé”®ç›˜å¯¼èˆª
 */
export function EmailVerification({
  email,
  userName = '',
  type = 'registration',
  onVerificationSuccess,
  onCancel,
  className = ''
}: EmailVerificationProps) {
  const [code, setCode] = useState(['', '', '', '', '', ''])
  const [isVerifying, setIsVerifying] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const [remainingTime, setRemainingTime] = useState(0)
  const [errorMessage, setErrorMessage] = useState('')
  const [remainingAttempts, setRemainingAttempts] = useState(3)
  
  const inputRefs = useRef<(HTMLInputElement | null)[]>([])
  const { showSuccess, showError } = useToast()

  // åˆå§‹åŒ–æ—¶è‡ªåŠ¨å‘é€éªŒè¯ç 
  useEffect(() => {
    sendVerificationCode()
  }, [email])

  // å€’è®¡æ—¶æ•ˆæœ
  useEffect(() => {
    if (remainingTime > 0) {
      const timer = setTimeout(() => {
        setRemainingTime(prev => prev - 1)
      }, 1000)
      return () => clearTimeout(timer)
    }
  }, [remainingTime])

  // å‘é€éªŒè¯ç 
  const sendVerificationCode = async () => {
    setIsSending(true)
    setErrorMessage('')

    try {
      let result
      if (type === 'registration') {
        result = await verificationManager.sendRegistrationCode(email, userName)
      } else {
        result = await verificationManager.sendLoginCode(email, userName)
      }

      if (result.success) {
        showSuccess(result.message)
        // è®¾ç½®å€’è®¡æ—¶ï¼ˆæ ¹æ®ç±»å‹ï¼‰
        const expiredMinutes = type === 'registration' ? 10 : 5
        setRemainingTime(expiredMinutes * 60)
        
        // å¼€å‘ç¯å¢ƒæ˜¾ç¤ºéªŒè¯ç 
        if (process.env.NODE_ENV === 'development' && result.code) {
          console.log(`éªŒè¯ç ï¼ˆå¼€å‘ç¯å¢ƒï¼‰: ${result.code}`)
          showSuccess(`éªŒè¯ç ï¼ˆå¼€å‘ç¯å¢ƒï¼‰: ${result.code}`)
        }
      } else {
        setErrorMessage(result.message)
        showError(result.message)
      }
    } catch (error) {
      const message = 'å‘é€éªŒè¯ç å¤±è´¥ï¼Œè¯·ç¨åé‡è¯•'
      setErrorMessage(message)
      showError(message)
    } finally {
      setIsSending(false)
    }
  }

  // å¤„ç†éªŒè¯ç è¾“å…¥
  const handleCodeChange = (index: number, value: string) => {
    // åªå…è®¸æ•°å­—
    if (!/^\d*$/.test(value)) return

    const newCode = [...code]
    newCode[index] = value

    setCode(newCode)
    setErrorMessage('')

    // è‡ªåŠ¨èšç„¦ä¸‹ä¸€ä¸ªè¾“å…¥æ¡†
    if (value && index < 5) {
      inputRefs.current[index + 1]?.focus()
    }

    // å¦‚æœè¾“å…¥å®Œæ•´éªŒè¯ç ï¼Œè‡ªåŠ¨æäº¤
    if (newCode.every(digit => digit !== '') && newCode.join('').length === 6) {
      verifyCode(newCode.join(''))
    }
  }

  // å¤„ç†é”®ç›˜äº‹ä»¶
  const handleKeyDown = (index: number, e: React.KeyboardEvent) => {
    if (e.key === 'Backspace' && !code[index] && index > 0) {
      // å¦‚æœå½“å‰è¾“å…¥æ¡†ä¸ºç©ºä¸”æŒ‰é€€æ ¼é”®ï¼Œèšç„¦ä¸Šä¸€ä¸ªè¾“å…¥æ¡†
      inputRefs.current[index - 1]?.focus()
    } else if (e.key === 'ArrowLeft' && index > 0) {
      inputRefs.current[index - 1]?.focus()
    } else if (e.key === 'ArrowRight' && index < 5) {
      inputRefs.current[index + 1]?.focus()
    }
  }

  // éªŒè¯éªŒè¯ç 
  const verifyCode = async (inputCode: string) => {
    if (isVerifying) return

    setIsVerifying(true)
    setErrorMessage('')

    try {
      const result = verificationManager.verifyCode(email, inputCode, type)

      if (result.success) {
        showSuccess('éªŒè¯æˆåŠŸï¼')
        onVerificationSuccess(email)
      } else {
        setErrorMessage(result.message)
        
        if (result.remainingAttempts !== undefined) {
          setRemainingAttempts(result.remainingAttempts)
        }

        if (result.isExpired || result.attemptsExceeded) {
          // æ¸…ç©ºéªŒè¯ç è¾“å…¥
          setCode(['', '', '', '', '', ''])
          inputRefs.current[0]?.focus()
        }
      }
    } catch (error) {
      const message = 'éªŒè¯å¤±è´¥ï¼Œè¯·é‡è¯•'
      setErrorMessage(message)
      showError(message)
    } finally {
      setIsVerifying(false)
    }
  }

  // æ‰‹åŠ¨éªŒè¯
  const handleVerify = () => {
    const inputCode = code.join('')
    if (inputCode.length === 6) {
      verifyCode(inputCode)
    } else {
      setErrorMessage('è¯·è¾“å…¥å®Œæ•´çš„6ä½éªŒè¯ç ')
    }
  }

  // æ¸…ç©ºè¾“å…¥
  const clearCode = () => {
    setCode(['', '', '', '', '', ''])
    setErrorMessage('')
    inputRefs.current[0]?.focus()
  }

  const canResend = remainingTime === 0 && !isSending

  return (
    <div className={`email-verification ${className}`}>
      <div className="verification-header">
        <h3 className="verification-title">
          {type === 'registration' ? 'é‚®ç®±éªŒè¯' : 'ç™»å½•éªŒè¯'}
        </h3>
        <p className="verification-subtitle">
          éªŒè¯ç å·²å‘é€è‡³ <strong>{email}</strong>
          <br />
          è¯·æŸ¥æ”¶é‚®ä»¶å¹¶è¾“å…¥6ä½éªŒè¯ç 
        </p>
      </div>

      {/* éªŒè¯ç è¾“å…¥æ¡† */}
      <div className="verification-code-container">
        <div className="code-inputs">
          {code.map((digit, index) => (
            <input
              key={index}
              ref={(el) => (inputRefs.current[index] = el)}
              type="text"
              inputMode="numeric"
              maxLength={1}
              value={digit}
              onChange={(e) => handleCodeChange(index, e.target.value)}
              onKeyDown={(e) => handleKeyDown(index, e)}
              className={`code-input ${errorMessage ? 'error' : ''}`}
              disabled={isVerifying}
              autoFocus={index === 0}
            />
          ))}
        </div>
        
        {errorMessage && (
          <div className="error-message">
            {errorMessage}
            {remainingAttempts > 0 && (
              <span className="remaining-attempts">
                ï¼ˆå‰©ä½™{remainingAttempts}æ¬¡æœºä¼šï¼‰
              </span>
            )}
          </div>
        )}
      </div>

      {/* æ“ä½œæŒ‰é’® */}
      <div className="verification-actions">
        <button
          onClick={handleVerify}
          disabled={isVerifying || code.join('').length !== 6}
          className="verify-button"
        >
          {isVerifying ? 'éªŒè¯ä¸­...' : 'éªŒè¯'}
        </button>

        <button
          onClick={clearCode}
          disabled={isVerifying}
          className="clear-button"
        >
          æ¸…ç©º
        </button>

        <button
          onClick={sendVerificationCode}
          disabled={!canResend}
          className="resend-button"
        >
          {isSending ? 'å‘é€ä¸­...' : canResend ? 'é‡æ–°å‘é€' : `${formatTimeRemaining(remainingTime)} åå¯é‡å‘`}
        </button>
      </div>

      {/* å–æ¶ˆæŒ‰é’® */}
      {onCancel && (
        <div className="verification-footer">
          <button
            onClick={onCancel}
            className="cancel-button"
            disabled={isVerifying || isSending}
          >
            å–æ¶ˆéªŒè¯
          </button>
        </div>
      )}

      <style jsx>{`
        .email-verification {
          max-width: 400px;
          margin: 0 auto;
          padding: 2rem;
          background: var(--color-bg-glass);
          backdrop-filter: blur(12px);
          border-radius: 16px;
          border: 1px solid var(--color-border-glass);
        }

        .verification-header {
          text-align: center;
          margin-bottom: 2rem;
        }

        .verification-title {
          font-size: var(--font-size-lg);
          font-weight: 600;
          color: var(--color-text-primary);
          margin-bottom: 0.5rem;
        }

        .verification-subtitle {
          font-size: var(--font-size-sm);
          color: var(--color-text-secondary);
          line-height: 1.5;
        }

        .verification-code-container {
          margin-bottom: 2rem;
        }

        .code-inputs {
          display: flex;
          gap: 0.5rem;
          justify-content: center;
          margin-bottom: 1rem;
        }

        .code-input {
          width: 3rem;
          height: 3rem;
          text-align: center;
          font-size: var(--font-size-lg);
          font-weight: 600;
          background: var(--color-bg-primary);
          border: 2px solid var(--color-border-secondary);
          border-radius: 8px;
          color: var(--color-text-primary);
          transition: all 0.2s ease;
        }

        .code-input:focus {
          outline: none;
          border-color: var(--color-primary-blue);
          box-shadow: 0 0 0 3px var(--color-primary-blue-alpha);
        }

        .code-input.error {
          border-color: var(--color-error);
        }

        .code-input:disabled {
          background: var(--color-bg-secondary);
          color: var(--color-text-disabled);
        }

        .error-message {
          text-align: center;
          font-size: var(--font-size-sm);
          color: var(--color-error);
          background: var(--color-error-bg);
          padding: 0.5rem;
          border-radius: 6px;
          border: 1px solid var(--color-error-border);
        }

        .remaining-attempts {
          color: var(--color-text-secondary);
          font-weight: normal;
        }

        .verification-actions {
          display: flex;
          gap: 0.75rem;
          margin-bottom: 1rem;
        }

        .verification-actions button {
          flex: 1;
          padding: 0.75rem;
          border-radius: 8px;
          font-size: var(--font-size-sm);
          font-weight: 500;
          transition: all 0.2s ease;
          cursor: pointer;
        }

        .verify-button {
          background: var(--color-primary-blue);
          color: white;
          border: none;
        }

        .verify-button:hover:not(:disabled) {
          background: var(--color-primary-blue-hover);
        }

        .verify-button:disabled {
          background: var(--color-bg-secondary);
          color: var(--color-text-disabled);
          cursor: not-allowed;
        }

        .clear-button,
        .resend-button {
          background: var(--color-bg-secondary);
          color: var(--color-text-secondary);
          border: 1px solid var(--color-border-secondary);
        }

        .clear-button:hover:not(:disabled),
        .resend-button:hover:not(:disabled) {
          background: var(--color-bg-hover);
          color: var(--color-text-primary);
        }

        .clear-button:disabled,
        .resend-button:disabled {
          color: var(--color-text-disabled);
          cursor: not-allowed;
        }

        .verification-footer {
          text-align: center;
          padding-top: 1rem;
          border-top: 1px solid var(--color-border-secondary);
        }

        .cancel-button {
          background: none;
          border: none;
          color: var(--color-text-secondary);
          font-size: var(--font-size-sm);
          cursor: pointer;
          padding: 0.5rem 1rem;
          border-radius: 6px;
          transition: color 0.2s ease;
        }

        .cancel-button:hover:not(:disabled) {
          color: var(--color-error);
        }

        .cancel-button:disabled {
          color: var(--color-text-disabled);
          cursor: not-allowed;
        }

        @media (max-width: 768px) {
          .email-verification {
            padding: 1.5rem;
            margin: 1rem;
          }

          .code-input {
            width: 2.5rem;
            height: 2.5rem;
            font-size: var(--font-size-base);
          }

          .verification-actions {
            flex-direction: column;
          }
        }
      `}</style>
    </div>
  )
}