'use client'

import { Icon } from '@/components/ui'

interface PaginationProps {
    currentPage: number
    totalPages: number
    onPageChange: (page: number) => void
    className?: string
    // å¯é€‰çš„è®°å½•ä¿¡æ¯æ˜¾ç¤º
    totalRecords?: number
    recordsPerPage?: number
    showInfo?: boolean
}

// ç»Ÿä¸€çš„åˆ†é¡µè®¾è®¡ç³»ç»Ÿ
const PAGINATION_STYLES = {
    // ç»Ÿä¸€å­—ä½“ç³»ç»Ÿ
    fontFamily: "'Alibaba PuHuiTi 3.0', sans-serif",
    fontSize: '14px',
    fontWeight: '400',

    // ç»Ÿä¸€é¢œè‰²ç³»ç»Ÿ - ä½¿ç”¨ä¸»é¢˜å˜é‡
    colors: {
        primary: 'var(--color-text-primary)',
        secondary: 'var(--color-text-secondary)',
        muted: 'var(--color-text-muted)',
        disabled: 'var(--color-text-disabled)',
        border: 'var(--color-border-primary)',
        activeBg: 'var(--color-hover-primary)',
        activeBorder: 'var(--color-border-active)',
        hoverBg: 'var(--color-hover-secondary)'
    },

    // ç»Ÿä¸€é—´è·ç³»ç»Ÿ
    spacing: {
        gap: '8px',
        padding: '8px 12px',
        borderRadius: '6px'
    },

    // ç»Ÿä¸€åŠ¨ç”»
    transition: 'all 0.2s ease'
}

export function Pagination({
    currentPage,
    totalPages,
    onPageChange,
    className = '',
    totalRecords,
    recordsPerPage,
    showInfo = false
}: PaginationProps) {

    // ç»Ÿä¸€çš„æŒ‰é’®æ ·å¼
    const getButtonStyle = (isActive = false, isDisabled = false) => ({
        fontFamily: PAGINATION_STYLES.fontFamily,
        fontSize: PAGINATION_STYLES.fontSize,
        fontWeight: isActive ? '600' : PAGINATION_STYLES.fontWeight,
        border: `1px solid ${isActive ? PAGINATION_STYLES.colors.activeBorder : PAGINATION_STYLES.colors.border}`,
        borderRadius: PAGINATION_STYLES.spacing.borderRadius,
        background: isActive ? PAGINATION_STYLES.colors.activeBg : 'transparent',
        color: isDisabled ? PAGINATION_STYLES.colors.disabled : PAGINATION_STYLES.colors.primary,
        cursor: isDisabled ? 'not-allowed' : 'pointer',
        transition: PAGINATION_STYLES.transition,
        whiteSpace: 'nowrap' as const,
        flexShrink: 0,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: PAGINATION_STYLES.spacing.padding
    })

    // æ™ºèƒ½é¡µç ç”Ÿæˆç®—æ³•
    const getPageNumbers = () => {
        const pages: (number | 'ellipsis')[] = []

        if (totalPages <= 5) {
            // 5é¡µä»¥å†…ï¼Œæ˜¾ç¤ºæ‰€æœ‰é¡µç 
            for (let i = 1; i <= totalPages; i++) {
                pages.push(i)
            }
        } else {
            // å¤§äº5é¡µï¼Œä½¿ç”¨çœç•¥å·ç®—æ³•
            if (currentPage <= 3) {
                // å½“å‰é¡µé è¿‘å¼€å¤´
                pages.push(1, 2, 3, 4, 'ellipsis', totalPages)
            } else if (currentPage >= totalPages - 2) {
                // å½“å‰é¡µé è¿‘ç»“å°¾
                pages.push(1, 'ellipsis', totalPages - 3, totalPages - 2, totalPages - 1, totalPages)
            } else {
                // å½“å‰é¡µåœ¨ä¸­é—´
                pages.push(1, 'ellipsis', currentPage - 1, currentPage, currentPage + 1, 'ellipsis', totalPages)
            }
        }

        return pages
    }

    const pageNumbers = getPageNumbers()

    return (
        <div
            className={className}
            style={{
                display: 'flex',
                justifyContent: showInfo ? 'space-between' : 'center',
                alignItems: 'center',
                gap: '16px',
                fontFamily: PAGINATION_STYLES.fontFamily,
                fontSize: PAGINATION_STYLES.fontSize,
                fontWeight: PAGINATION_STYLES.fontWeight,
                flexWrap: 'nowrap'
            }}
        >
            {/* å¯é€‰çš„è®°å½•ä¿¡æ¯æ˜¾ç¤º */}
            {showInfo && totalRecords && recordsPerPage && (
                <span style={{
                    color: PAGINATION_STYLES.colors.muted,
                    fontSize: PAGINATION_STYLES.fontSize,
                    lineHeight: '20px',
                    whiteSpace: 'nowrap',
                    flexShrink: 0
                }}>
                    å…± {totalRecords} æ¡è®°å½•
                </span>
            )}

            {/* åˆ†é¡µå¯¼èˆª */}
            <nav style={{
                display: 'flex',
                gap: PAGINATION_STYLES.spacing.gap,
                alignItems: 'center',
                flexShrink: 0
            }}>
                {/* ä¸Šä¸€é¡µç®­å¤´ */}
                <button
                    onClick={() => onPageChange(currentPage - 1)}
                    disabled={currentPage === 1}
                    style={{
                        ...getButtonStyle(false, currentPage === 1),
                        width: '32px',
                        height: '32px',
                        padding: '0'
                    }}
                    title="ä¸Šä¸€é¡µ"
                >
                    <Icon
                        name="arrow-left"
                        size="sm"
                        style={{
                            color: currentPage === 1 ?
                                PAGINATION_STYLES.colors.disabled :
                                PAGINATION_STYLES.colors.primary
                        }}
                    />
                </button>

                {/* é¡µç æŒ‰é’®å’Œçœç•¥å· */}
                {pageNumbers.map((page, index) => {
                    if (page === 'ellipsis') {
                        return (
                            <span
                                key={`ellipsis-${index}`}
                                style={{
                                    color: PAGINATION_STYLES.colors.muted,
                                    fontSize: PAGINATION_STYLES.fontSize,
                                    padding: '0 4px',
                                    userSelect: 'none'
                                }}
                            >
                                ...
                            </span>
                        )
                    }

                    const isActive = page === currentPage

                    return (
                        <button
                            key={page}
                            onClick={() => onPageChange(page as number)}
                            style={{
                                ...getButtonStyle(isActive),
                                width: '32px',
                                height: '32px',
                                padding: '0'
                            }}
                            onMouseEnter={(e) => {
                                if (!isActive) {
                                    e.currentTarget.style.background = PAGINATION_STYLES.colors.hoverBg
                                }
                            }}
                            onMouseLeave={(e) => {
                                if (!isActive) {
                                    e.currentTarget.style.background = 'transparent'
                                }
                            }}
                        >
                            {page}
                        </button>
                    )
                })}

                {/* ä¸‹ä¸€é¡µç®­å¤´ */}
                <button
                    onClick={() => onPageChange(currentPage + 1)}
                    disabled={currentPage === totalPages}
                    style={{
                        ...getButtonStyle(false, currentPage === totalPages),
                        width: '32px',
                        height: '32px',
                        padding: '0'
                    }}
                    title="ä¸‹ä¸€é¡µ"
                >
                    <Icon
                        name="arrow-right"
                        size="sm"
                        style={{
                            color: currentPage === totalPages ?
                                PAGINATION_STYLES.colors.disabled :
                                PAGINATION_STYLES.colors.primary
                        }}
                    />
                </button>
            </nav>
        </div>
    )
} 