'use client'

import { useState, useEffect, useRef, useCallback } from 'react'
import { Icon } from '@/components/ui'

interface SearchBarProps {
    placeholder?: string
    onSearch: (query: string) => void
    className?: string
    disabled?: boolean
    isLoading?: boolean
}

export function SearchBar({
    placeholder = "æœç´¢æ–‡ç« ã€å·¥å…·ã€æ¡ˆä¾‹...", // P0-1: ç®€åŒ–å ä½ç¬¦æ–‡å­—
    onSearch,
    className = '',
    disabled = false,
    isLoading = false
}: SearchBarProps) {
    const [query, setQuery] = useState('')
    const [isFocused, setIsFocused] = useState(false)
    const [showShortcut, setShowShortcut] = useState(true)
    const [shortcutKey, setShortcutKey] = useState('') // ä¿®å¤SSRé—®é¢˜ï¼šåˆå§‹ä¸ºç©º
    const [isClient, setIsClient] = useState(false) // è¿½è¸ªæ˜¯å¦åœ¨å®¢æˆ·ç«¯
    const inputRef = useRef<HTMLInputElement>(null)
    const debounceTimeoutRef = useRef<NodeJS.Timeout | null>(null)

    // ä¿®å¤SSRé—®é¢˜ï¼šåªåœ¨å®¢æˆ·ç«¯è®¾ç½®å¿«æ·é”®
    useEffect(() => {
        setIsClient(true)
        const isMac = typeof navigator !== 'undefined' && navigator.platform.toUpperCase().indexOf('MAC') >= 0
        setShortcutKey(isMac ? 'âŒ˜K' : 'Ctrl+K')
    }, [])

    // é”®ç›˜å¿«æ·é”®æ”¯æŒ
    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            // Ctrl+K æˆ– Cmd+K èšç„¦æœç´¢æ¡†
            if ((e.ctrlKey || e.metaKey) && e.key === 'k') {
                e.preventDefault()
                inputRef.current?.focus()
            }
        }

        document.addEventListener('keydown', handleKeyDown)
        return () => document.removeEventListener('keydown', handleKeyDown)
    }, [])

    // æ£€æµ‹é”®ç›˜å¿«æ·é”®æ˜¾ç¤º
    useEffect(() => {
        setShowShortcut(!isFocused && !query && !isLoading && isClient && !!shortcutKey)
    }, [isFocused, query, isLoading, isClient, shortcutKey])

    // é˜²æŠ–æœç´¢ - P0-2: ä¼˜åŒ–æœç´¢é—¨æ§›ï¼Œ1å­—ç¬¦æ—¶å»¶è¿Ÿæ›´é•¿
    const debouncedSearch = useCallback((searchQuery: string, delay?: number) => {
        if (debounceTimeoutRef.current) {
            clearTimeout(debounceTimeoutRef.current)
        }

        const debounceDelay = delay || (searchQuery.length === 1 ? 500 : 300) // 1å­—ç¬¦500msï¼Œå…¶ä»–300ms
        debounceTimeoutRef.current = setTimeout(() => {
            onSearch(searchQuery.trim())
        }, debounceDelay)
    }, [onSearch])

    // æ¸…ç†é˜²æŠ–å®šæ—¶å™¨
    useEffect(() => {
        return () => {
            if (debounceTimeoutRef.current) {
                clearTimeout(debounceTimeoutRef.current)
            }
        }
    }, [])

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault()
        // ç«‹å³æœç´¢ï¼Œæ¸…é™¤é˜²æŠ–
        if (debounceTimeoutRef.current) {
            clearTimeout(debounceTimeoutRef.current)
        }
        onSearch(query.trim())
    }

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value
        setQuery(value)

        // P0-2: ä¼˜åŒ–æœç´¢é—¨æ§› - 1å­—ç¬¦å³å¯æœç´¢ï¼Œä½†å»¶è¿Ÿæ›´é•¿
        if (value.trim().length >= 1) {
            debouncedSearch(value)
        } else if (value.trim().length === 0) {
            // ç«‹å³æ¸…ç©ºç»“æœ
            if (debounceTimeoutRef.current) {
                clearTimeout(debounceTimeoutRef.current)
            }
            onSearch('')
        }
    }

    const handleClear = () => {
        setQuery('')
        onSearch('')
        inputRef.current?.focus()
        // æ¸…é™¤é˜²æŠ–å®šæ—¶å™¨
        if (debounceTimeoutRef.current) {
            clearTimeout(debounceTimeoutRef.current)
        }
    }

    // é”®ç›˜å¿«æ·é”®æ”¯æŒ
    const handleKeyDown = (e: React.KeyboardEvent) => {
        switch (e.key) {
            case 'Escape':
                // ESCé”®æ¸…ç©ºæœç´¢
                handleClear()
                e.preventDefault()
                break
            case 'Enter':
                // Enteré”®ç¡®è®¤æœç´¢
                handleSubmit(e as React.FormEvent)
                break
        }
    }

    // ç„¦ç‚¹å¤„ç†
    const handleFocus = () => {
        setIsFocused(true)
    }

    const handleBlur = () => {
        setIsFocused(false)
    }

    // P0-3: ä¿®å¤SSRé—®é¢˜çš„å ä½ç¬¦æ–‡æœ¬ç”Ÿæˆ
    const getPlaceholderText = () => {
        if (isLoading) return "æœç´¢ä¸­..."
        if (showShortcut && shortcutKey) {
            return `${placeholder} (${shortcutKey})`
        }
        return placeholder
    }

    return (
        <div className={className}>
            <form onSubmit={handleSubmit} role="search">
                {/* å¤–å±‚æ¯›ç»ç’ƒå®¹å™¨ - æœªèšç„¦æ—¶æ— è¾¹æ¡† */}
                <div style={{
                    background: 'transparent', // ä¿æŒå®Œå…¨é€æ˜
                    backdropFilter: 'blur(8px)', // å‡å°‘æ¨¡ç³Šæ•ˆæœ
                    WebkitBackdropFilter: 'blur(8px)',
                    border: isFocused
                        ? '1px solid rgba(59, 130, 246, 0.40)' // èšç„¦æ—¶è“è‰²è¾¹æ¡†
                        : '1px solid transparent', // éèšç„¦æ—¶é€æ˜è¾¹æ¡†ï¼ˆæ— è¾¹æ¡†æ•ˆæœï¼‰
                    borderRadius: '16px',
                    padding: '16px 20px',
                    width: '100%',
                    transition: 'all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94)',
                    opacity: disabled ? 0.5 : 1,
                    // ä¿æŒå¾®å¦™çš„é˜´å½±æ•ˆæœ
                    boxShadow: isFocused
                        ? '0 4px 20px rgba(59, 130, 246, 0.15)'
                        : 'none', // æœªèšç„¦æ—¶æ— é˜´å½±
                }}>
                    {/* å†…å±‚æœç´¢è¾“å…¥æ¡† - ä¼˜åŒ–è§†è§‰è®¾è®¡ */}
                    <div className="search-container-no-border" style={{
                        background: 'var(--color-bg-input)', // ä½¿ç”¨ä¸»é¢˜å˜é‡
                        border: 'none',
                        borderTop: 'none',
                        borderBottom: 'none',
                        borderLeft: 'none',
                        borderRight: 'none',
                        outline: 'none',
                        boxShadow: 'none',
                        borderRadius: '12px',
                        display: 'flex',
                        alignItems: 'center',
                        gap: '12px', // å¢åŠ é—´è·è®©å¸ƒå±€æ›´èˆ’é€‚
                        paddingLeft: '18px', // ä¼˜åŒ–å†…è¾¹è·
                        paddingRight: '18px',
                        paddingTop: '2px',
                        paddingBottom: '2px',
                        overflow: 'hidden',
                        position: 'relative',
                        minHeight: '44px' // ç¡®ä¿æœ€å°é«˜åº¦ï¼Œä¼˜åŒ–è§†è§‰æ¯”ä¾‹
                    }}>
                        {/* æœç´¢å›¾æ ‡æˆ–åŠ è½½æŒ‡ç¤ºå™¨ */}
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            minWidth: '18px',
                            minHeight: '18px'
                        }}>
                            {isLoading ? (
                                // åŠ è½½çŠ¶æ€ - æ—‹è½¬åŠ¨ç”»
                                <div
                                    style={{
                                        width: '18px',
                                        height: '18px',
                                        border: '2px solid rgba(59, 130, 246, 0.2)',
                                        borderTop: '2px solid #3B82F6',
                                        borderRadius: '50%',
                                        animation: 'spin 1s linear infinite'
                                    }}
                                />
                            ) : (
                                <Icon
                                    name="search-icon"
                                    size="md"
                                    style={{
                                        width: '18px', // ç¨å¾®å‡å°å°ºå¯¸ï¼Œæ›´ç²¾è‡´
                                        height: '18px',
                                        flexShrink: 0,
                                        transition: 'filter 0.3s ease',
                                        // ä¼˜åŒ–é¢œè‰²è¿‡æ¸¡
                                        filter: isFocused
                                            ? 'brightness(0) saturate(100%) invert(47%) sepia(65%) saturate(2976%) hue-rotate(214deg) brightness(102%) contrast(98%)'
                                            : 'brightness(0) saturate(100%) invert(75%) sepia(8%) saturate(345%) hue-rotate(183deg) brightness(95%) contrast(92%)' // ç¨å¾®æ›´äº®çš„ç°è‰²
                                    }}
                                />
                            )}
                        </div>

                        {/* æœç´¢è¾“å…¥æ¡† - ç²¾ç¡®è¿˜åŸè®¾è®¡ç¨¿æ ·å¼ */}
                        <input
                            ref={inputRef}
                            type="text"
                            value={query}
                            onChange={handleChange}
                            onKeyDown={handleKeyDown}
                            onFocus={() => setIsFocused(true)}
                            onBlur={() => setIsFocused(false)}
                            placeholder={placeholder}
                            disabled={disabled}
                            style={{
                                flex: 1,
                                height: '100%',
                                background: 'transparent',
                                border: 'none',
                                outline: 'none',
                                fontSize: 'var(--font-size-lg)',
                                fontFamily: 'var(--font-family-primary)',
                                color: query ? 'var(--color-text-primary)' : 'var(--color-text-muted)',
                                padding: '0 var(--spacing-3)',
                                transition: 'color 0.2s ease'
                            }}
                            className="search-bar-input"
                        />

                        {/* æ¸…ç©ºæŒ‰é’® - ä¼˜åŒ–è§†è§‰è®¾è®¡ */}
                        {query && !disabled && (
                            <button
                                type="button"
                                onClick={handleClear}
                                aria-label="æ¸…ç©ºæœç´¢"
                                style={{
                                    background: 'transparent', // æ”¹ä¸ºé€æ˜èƒŒæ™¯
                                    border: '1px solid var(--color-border-primary)', // ä½¿ç”¨ä¸»é¢˜è¾¹æ¡†è‰²
                                    borderRadius: '50%',
                                    width: '24px', // ç¨å¾®å‡å°å°ºå¯¸
                                    height: '24px',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    cursor: 'pointer',
                                    color: 'var(--color-text-muted)', // ä½¿ç”¨ä¸»é¢˜æ–‡å­—è‰²
                                    fontSize: '12px',
                                    lineHeight: '1',
                                    flexShrink: 0,
                                    transition: 'all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94)', // æ›´å¹³æ»‘çš„è¿‡æ¸¡
                                    opacity: 0.6
                                }}
                                onMouseEnter={(e) => {
                                    e.currentTarget.style.background = 'var(--color-primary-blue)' // æ‚¬åœæ—¶ä½¿ç”¨ä¸»é¢˜è“è‰²
                                    e.currentTarget.style.borderColor = 'var(--color-primary-blue)'
                                    e.currentTarget.style.color = 'var(--color-bg-primary)' // ä½¿ç”¨èƒŒæ™¯è‰²çš„åè‰²
                                    e.currentTarget.style.opacity = '1'
                                    e.currentTarget.style.transform = 'scale(1.1)'
                                }}
                                onMouseLeave={(e) => {
                                    e.currentTarget.style.background = 'transparent'
                                    e.currentTarget.style.borderColor = 'var(--color-border-primary)'
                                    e.currentTarget.style.color = 'var(--color-text-muted)'
                                    e.currentTarget.style.opacity = '0.6'
                                    e.currentTarget.style.transform = 'scale(1)'
                                }}
                            >
                                âœ•
                            </button>
                        )}
                    </div>
                </div>
            </form>

            {/* å†…è”CSSæ ·å¼ */}
            <style jsx>{`
                @keyframes spin {
                    0% { transform: rotate(0deg); }
                    100% { transform: rotate(360deg); }
                }

                /* è¾“å…¥æ¡†èšç„¦æ—¶çš„é«˜äº®æ•ˆæœ */
                input:focus::placeholder {
                    color: rgba(156, 163, 175, 0.8);
                    transition: color 0.3s ease;
                }

                /* æœç´¢æ¡†æ•´ä½“çš„å¾®åŠ¨ç”» */
                form {
                    transition: transform 0.2s ease;
                }

                form:focus-within {
                    transform: translateY(-1px);
                }
            `}</style>
        </div>
    )
}